import React, { useEffect, useState } from 'react';
import {Animated} from "react-animated-css";
import styled from 'styled-components';
import image1 from '../../img/squat.gif';
import image2 from '../../img/lunge.gif';
import image3 from '../../img/shoulderpress.gif';
import image4 from '../../img/tree.png';
import './First.css';
import Button from '../../components/common/Button';
import palette from '../../lib/style/palette';
import Responsive from '../../components/common/Responsive';
import FirstExer from '../../components/exercises/FirstExer';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
const Hello = styled(Responsive)`
    text-align : center;
    
`;
const Wrapper1 = styled.div`
font-size : 16px;
margin : 0 auto;
height: 600px;
@media (max-width: 768px) {
    height : 750px;
};
@media (max-width: 500px) {
    height : 850px;
};
`;

const Wrapper2 = styled.div`
font-size : 16px;
margin : 0 auto;
height: 630px;
`;


const Spacer = styled.div`
  height: 4rem;
  `;

  const StyledButtonPre = styled(Button)`
  background: ${palette.gray[6]};
  &:hover {
    background: ${palette.gray[5]};
  }
    font-size : 0.8rem;
    margin-right: 24rem;
    display : inline-block;
    padding-left : 5px;
    @media (max-width: 1100px) {
        margin-right: 8rem;
    };
    @media (max-width: 500px) {
        margin-right: 1rem;
    };
`;
const StyledButtonNext = styled(Button)`
background: ${palette.gray[6]};
&:hover {
  background: ${palette.gray[5]};
}
  font-size : 0.8rem;
  margin-left: 24rem;
  display : inline-block;
  padding-right : 5px;
  @media (max-width: 1100px) {
      margin-left: 8rem;
  };
  @media (max-width: 500px) {
    margin-left: 1rem;
};
`;
const Order = styled.div`
@media screen (max-width: 768px) {
  align-items : center;
};
`
const First = ({setSquatCount, setLungeCount, setShoulderCount}) => {
       const [timeover, setTimeover] = useState(false);
       const [visible1, setVisible1] = useState(true);
       const [visible2, setVisible2] = useState(true);
       const [exerciseEx, setExerciseEx] = useState(false);
       const [pose, setPose] = useState(0);
       const styleExplain = {
        fontSize : "1.3rem",
        textAlign : "center"
    };
       let time1=0;
       let menus = ["", " .", "     4 ."];
        let menuList = menus.map((menu) => {
            return <Animated 
                    animationIn="fadeIn" 
                    animationInDelay={1000} 
                    animationOut="fadeOut" 
                    animationOutDelay={1000} 
                    isVisible={visible1}
                    style = {styleExplain}>{menu}</Animated>
        });
    
     useEffect(() => {
        setTimeout(function() {
            setVisible1(false);
        }, 4000);
        setTimeout(function() {
            setTimeover(true);
        }, 6000);
     }, []);

     const nextPose = () => {
        if (pose === 0)
            setPose(1);
        else if (pose === 1)
            setPose(2);
        else if (pose === 2)
            setPose(3);
        else
            setPose(0);
     };
     const prePose = () => {
         if (pose === 1)
            setPose(0);
        else if (pose === 2)
            setPose(1);
        else if (pose === 3)
            setPose(2);
        else
            setPose(3);
     }
     const showExer = () => {
         setVisible2(false);
         setTimeout(function() {
            setExerciseEx(true);
         }, 3000);
     }
     const setSCount = (e) => {
         setSquatCount(e.target.value);
     }
     const setLCount = (e) => {
        setLungeCount(e.target.value);
     }
     const setShCount = (e) => {
         setShoulderCount(e.target.value);
     }
    
    return (
        <>
        {!timeover &&
         <Wrapper1>
         <Spacer/><Spacer/>
         <Hello>{menuList}
         <Spacer/>
             <Animated
                 animationIn="fadeIn"
                 animationInDelay={2000}
                 animationOut="fadeOut"
                 animationOutDelay={1000}
                 isVisible={visible1}
             >
                 <FirstExer/>
             </Animated>
         </Hello>
     </Wrapper1>}
        {( timeover && !exerciseEx ) &&
        <Wrapper2>
            <div id="hello">
                <Spacer/>
                <Animated animationIn="fadeIn" animationInDelay={1000} animationOut="fadeOut" animationOutDelay={500} isVisible={visible2}>
                <div className="inputWrapper">
                <div id="setCount">     .<br/>   ,<br/>     .</div>
                    <table id="inputCount">
                        <tr id="squat">
                            <td>
                            
                            </td>
                            <td>
                            <input className="inputBox" type="number" onChange={setSCount} placeholder="15"/>
                            </td>
                            <div className="recomend" id="squEx"> 10~15 3~5set  .  10   .</div>
                        </tr>
                        <tr id="lunge">
                            <td>
                            
                            </td>
                            <td>
                            <input className="inputBox" type="number" onChange={setLCount} placeholder="15"/>
                            </td>
                            <div className="recomend" id="lunEx">    10~15 3~5set  .  10   .</div>
                        </tr>
                        <tr id="press">
                            <td>
                            
                            </td>
                            <td>
                            <input className="inputBox" type="number" onChange={setShCount} placeholder="15"/>
                            </td>
                            <div className="recomend" id="preEx"> 6~15 4set  .            .      .</div>
                        </tr>
                    </table>
                    <div id="showExer" onClick={showExer}> </div>
                    </div>
                </Animated>
            </div>
        </Wrapper2>}
        {exerciseEx && 
        <Animated animationIn="fadeIn" animationInDelay={500}>
            <Spacer/>
        <div className="ex">
                {(pose === 0) && <Animated animationIn="fadeIn" animationInDelay={500}>
                <div id="pose">SQUAT</div>
                <img id="image" src={image1}/>
                <span id="posetext">
                    <ol>
                        <li>       .</li>
                        <li>    .</li>
                        <li>      ,    15~20  .</li>
                        <li>            .</li>
                        <li>      .</li>
                    </ol>
                </span>
                </Animated>}
                {(pose === 1) && <Animated animationIn="fadeIn" animationInDelay={500}>
                <div id="pose">SIDE LUNGE</div>
                <img id="image" src={image2}/>
                <span id="posetext">
                    <ol>
                        <li>   .</li>
                        <li>    .</li>
                        <li>   2  .</li>
                        <li>                       .</li>
                        <li>        .</li>
                    </ol>
                </span>
                </Animated>}
                {(pose === 2) && <Animated animationIn="fadeIn" animationInDelay={500}>
                <div id="pose">SHOULDER PRESS</div>
                <img id="image" src={image3}/>
                <span id="posetext">
                    <ol>
                        <li>   .</li>
                        <li>   .</li>
                        <li>    .</li>
                        <li>   ,         .</li>
                        <li>  .</li>
                    </ol>
                </span>
                </Animated>}
                {(pose === 3) && <Animated animationIn="fadeIn" animationInDelay={500}>
                <div id="pose">TREE POSE</div>
                <img id="image" src={image4}/>
                <span id="posetext">
                    <ol>
                        <li>   .</li>
                        <li>   .</li>
                        <li>     .</li>
                        <li>         .</li>
                        <li>          .</li>
                    </ol>
                </span>
                </Animated>}
                <Order>
                    {exerciseEx && <StyledButtonPre onClick={prePose}><NavigateBeforeIcon/> </StyledButtonPre>}
                    {exerciseEx && <StyledButtonNext onClick={nextPose}> <NavigateNextIcon/></StyledButtonNext>}
                </Order>              
        </div>
        </Animated>}
        </>
    );
};
export default First;