import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Responsive from '../common/Responsive';
import Button from '../common/Button';
import palette from '../../lib/style/palette';
import SubInfo from '../common/SubInfo';
import { Link } from 'react-router-dom';

const PostListBlock = styled(Responsive)`
  margin-top: 3rem;
`;

const WritePostButtonWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-top: 5em;
`;

const PostItemBlock = styled.div`
  padding-top: 3rem;
  padding-bottom: 3rem;
  /*    padding-top  */
  &:first-child {
    padding-top: 0;
  }
  & + & {
    border-top: 1px solid ${palette.gray[2]};
  }
  h2 {
    font-size: 2rem;
    margin-bottom: 0;
    margin-top: 0;
    &:hover {
      color: ${palette.gray[6]};
    }
  }
  p {
    margin-top: 2rem;
  }
`;


const PostItem = ({ post }) => {
  const [usrlevel, setLevel] = useState('');
  const { publishedDate, user, title, body, _id } = post;


  return (
    <PostItemBlock>
      <h2>
        <Link to={`/@${user.username}/${_id}`} style={{color: 'black', textDecoration: 'none'}}>{title}</Link>
      </h2>
      {<SubInfo 
        username ={user.username} 
        userlevel={usrlevel}
        publishedDate={new Date(publishedDate)}/>}
      <p>{body}</p>
    </PostItemBlock>
  );
};

const PostList = ({ posts, loading, error, showWriteButton }) => {
  const [check, setCheck] = useState(false);
  useEffect(() => {
    if(posts && posts.length == 0){
      setCheck(true);   //   
    } else {
      setCheck(false);
    }
  }, [posts])

  if (error) {
    return <PostListBlock> .</PostListBlock>;
  }
  
  return (
    <PostListBlock>
      <WritePostButtonWrapper>
      {showWriteButton && (
          <Button to="/write" style={{textDecoration: 'none', color: 'white'}}>
              
          </Button>
        )}
      </WritePostButtonWrapper>
      {/*    ,      */}
      {check && 
        <div style={{fontSize: '1.5rem', fontWeight: 'bold', padding:'4% 0'}}>
           .</div>}

      {!loading && posts && (
        <div>
         {posts.map(post => (
          <PostItem post={post} key={post._id} />
        ))}
        </div>
      )}

    </PostListBlock>
  );
};

export default PostList;