import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import {Animated} from "react-animated-css";
import LoadingBar from '../common/LoadingBar';
import Responsive from '../common/Responsive';
import ExerciseResult from './ExerciseResult';

const Wrapper1 = styled.div`
  position:relative;
  text-align: center;
`;

const Wrapper2 = styled.div`
height : 500px;
  position:relative;
  text-align: center;
`;

const Loading = styled(Responsive)`
    display: flex;
    width: 100%;
    align-items: center;

`;

const Spacer = styled.div`
  height: 5rem;
  `;

const Check = ({analysis}) => {
    const [showResults, setShowResults] = useState(false);
    const [trainerAngle, setTrainer] = useState([]);
    const [result, setResult] = useState([]);
    const [loading, setLoading] = useState(false);
    const { user } = useSelector(({ user }) => ({
        user: user.user
      }));
    let mistakes = [   
      {
        squat: [],
        lungeL: [],
        lungeR: [],
        press: [],
        tree: []
      }
    ]
    let userAngle = [
      {
        exname: "",
        angle: []
      },
      {
        exname: "  ",
        angle: []
      },
      {
        exname: "  ",
        angle: []
      },
      {
        exname: " ",
        angle: []
      },
      {
        exname: " ",
        angle: []
      }
    ];
    const hours = parseInt(user.s/60/60);
    const minutes = parseInt(user.s%(60*60)/60);
    const seconds = parseInt(user.s%60);

    const angle = (exer) => {   //user  8   
      let idx = 0;
      if(exer.exname == "  "){
        idx = 1;
      }
      else if(exer.exname == "  "){
        idx = 2;
      }
      else if(exer.exname == ""){
        idx = 3;
      }
      else if(exer.exname == " "){
        idx = 4;
      }
      const inputX = exer.x, inputY = exer.y;
      //   (-)
      userAngle[idx].angle.push((Math.abs(Math.atan2(inputY[13] - inputY[11], inputX[13] - inputX[11])) + Math.abs(Math.atan2(inputY[5] - inputY[11], inputX[5] - inputX[11]))) * (180 / Math.PI));
      //   (-)
      userAngle[idx].angle.push(360 - (Math.abs(Math.atan2(inputY[14] - inputY[12], inputX[14] - inputX[12])) + Math.abs(Math.atan2(inputY[6] - inputY[12], inputX[6] - inputX[12]))) * (180 / Math.PI));
      //  ( )
      userAngle[idx].angle.push((Math.abs(Math.atan2(inputY[11] - inputY[5], inputX[11] - inputX[5])) + Math.abs(Math.atan2(inputY[7] - inputY[5], inputX[7] - inputX[5]))) * (180 / Math.PI));
      //  ( )
      userAngle[idx].angle.push(360 - (Math.abs(Math.atan2(inputY[8] - inputY[6], inputX[8] - inputX[6])) + Math.abs(Math.atan2(inputY[12] - inputY[6], inputX[12] - inputX[6]))) * (180 / Math.PI));
      // 
      userAngle[idx].angle.push((Math.abs(Math.atan2(inputY[9] - inputY[7], inputX[9] - inputX[7])) + Math.abs(Math.atan2(inputY[5] - inputY[7], inputX[5] - inputX[7]))) * (180 / Math.PI));
      //  
      userAngle[idx].angle.push(360 - (Math.abs(Math.atan2(inputY[10] - inputY[8], inputX[10] - inputX[8])) + Math.abs(Math.atan2(inputY[6] - inputY[8], inputX[6] - inputX[8]))) * (180 / Math.PI));
      // 
      userAngle[idx].angle.push((Math.abs((Math.atan2(inputY[15] - inputY[13], inputX[15] - inputX[13])) + Math.abs(Math.atan2(inputY[11] - inputY[13], inputX[11] - inputX[13])))) * (180 / Math.PI));
      // 
      userAngle[idx].angle.push(360 - (Math.abs((Math.atan2(inputY[16] - inputY[14], inputX[16] - inputX[14])) + Math.abs(Math.atan2(inputY[12] - inputY[14], inputX[12] - inputX[14])))) * (180 / Math.PI));
      console.log(userAngle[idx]);
     
      if(idx == 4){
        calculateAngle();
      }
    };

    const calculateAngle = () => {
       //   
      console.log(trainerAngle);
      let cmp = 0.0; // 
      let str = "";
      let squatms = [], lungeLms = [], lungeRms = [], pressms = [], treems = [];

      if(userAngle[0].angle[0] == 0){
        str = " ";
        squatms.push(str);
      } else {     //
        cmp = (360-trainerAngle[0]["0"]) - (360-userAngle[0].angle[0]);   //  
        if(cmp > 10){
          str = "   ";
          squatms.push(str);
        } 
        else if(cmp < -10){
          str = "   ";
          squatms.push(str);
        } 
        cmp = trainerAngle[0]["6"] - userAngle[0].angle[6]; //  
        if(cmp > 10){
          str = "   ";
          squatms.push(str);
        }
        else if(cmp < -10){
          str = "   ";
          squatms.push(str);
        }
      }
      if(userAngle[1].angle[6] == 0){
        str = " ";
        lungeLms.push(str);
      } else {
          //  
        cmp = (360-trainerAngle[1]["6"]) - (360-userAngle[1].angle[6]);  //  
        if(cmp < -10){
          str = "    ";
          lungeLms.push(str);
        } 
        cmp = (360-trainerAngle[1]["7"]) - (360-userAngle[1].angle[7]);   ///   
        if(cmp > 10 || cmp < -10){
          str = "    ";
          lungeLms.push(str);
        }
      }
      if(userAngle[2].angle[6] == 0){
        str = " ";
        lungeRms.push(str);
      } else {
        //  
        cmp = (360-trainerAngle[2]["6"]) - (360-userAngle[2].angle[6]);  //  
        if(cmp > 10 || cmp < -10){
          str = "    ";
          lungeRms.push(str);
        }
        cmp = (360-trainerAngle[2]["7"]) - (360-userAngle[2].angle[7]);   //  
        console.log(cmp);
        if(cmp < -10){
          str = "    ";
          lungeRms.push(str);
        }
      }
      if(userAngle[3].angle[3] == 360){
        str = " ";
        pressms.push(str);
      } else {
        // 
        cmp = trainerAngle[3]["3"] - userAngle[3].angle[3];  //  
        if(cmp > 10){
          str = "   ";
          pressms.push(str);
        }
        cmp = trainerAngle[3]["2"] - userAngle[3].angle[2];  //  
        if(cmp > 10){
          str = "   ";
          pressms.push(str);
        }
        cmp = (360-trainerAngle[3]["5"]) - (360-userAngle[3].angle[5]);   //  
        if(cmp > 10){
          str = "   ";
          pressms.push(str);
        }
        cmp = (360-trainerAngle[3]["4"]) - (360-userAngle[3].angle[4]);   //  
        if(cmp > 10){
          str = "   ";
          pressms.push(str);
        }
      }
      if(userAngle[4].angle[1] == 360){
        str = " ";
        treems.push(str);
      } else {
        // 
        cmp = trainerAngle[4]["1"] - userAngle[4].angle[1];   //  
        if(cmp < -10){
          str = "   ";
          treems.push(str);
        }
        cmp = (360-trainerAngle[4]["7"]) - (360-userAngle[4].angle[7]);   //    
        if(cmp < -10){
          str = "    ";
          treems.push(str);
        }
      }
      
      mistakes[0].squat = squatms;
      mistakes[0].lungeL = lungeLms;
      mistakes[0].lungeR = lungeRms;
      mistakes[0].press = pressms;
      mistakes[0].tree = treems;
      setResult(mistakes);
      setTimeout(()=> {
        setShowResults(true);
      }, 3000);
    }

    useEffect(() => {
        console.log(analysis);
        fetch(`https://smlistener.s3.ap-northeast-2.amazonaws.com/json/new_trainer_angle.json`)
        .then(results => results.json())
        .then(json => {
           setTrainer(json);
           setLoading(true);
        })
        
    }, [])   
    
    useEffect(() => {
      if(loading){
        analysis.map(exer => {
          angle(exer);
        });
        
      }
    }, [loading])

    
  return (
    <>
    {showResults && <Wrapper1> 
      <Spacer/>
      <Animated><ExerciseResult mistakes = { result }/> </Animated>
    </Wrapper1>}
    {!showResults &&<Wrapper2>
      <Loading>
         <LoadingBar done={100}/>
      </Loading>
      </Wrapper2>}  
      </>
  )
};


export default Check;